* {
	vertical-align: baseline;
	font-family: inherit;
	font-style: inherit;
	font-size: 100%;
	border: none;
	padding: 0;
	margin: 0;
  font-family: 'VT323', monospace;
	}
body {
	padding: 5px;
	background-color: #9da5b4;
	}

li, dd, blockquote {
	margin-left: 40px;
	list-style: none;
	}
table {
	border-collapse: collapse;
	border-spacing: 0;
	}
:active, :hover, :focus {
	outline: 0;
	outline-offset: 0;
	}
button {
	border: 1px solid grey;
}
#root {
	width: 100%;
}
.main-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}
.main-container,
.main-container > section {
	width: 90%;
	margin: 0 auto;
	display: -ms-flexbox;
	display: flex;
}
.crearte-task-container,
.task-container {
	width: 50%;
}
.header {
	margin-bottom: 40px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack:justify;
	    justify-content:space-between;
	height: 40px;
	color: #fff;
	background-color: #534D4D;
}
.header button {
	margin: 5px;
}
.header h3 {
height: 40px;
line-height: 40px;
}
.header section {
	display: -ms-flexbox;
	display: flex;
}
.create-input-container {
	width: 100%;
}

.create-input-container input {
	width: 80%;
	height: 40px;
	margin-top: 10px;
	font-size: 16px;
}
.create-input-container section {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}
.create-input-container :nth-child(4) {
	width: 60%;
}
.create-input-container button {
	margin: 0 auto;
	width: 20%;
	height: 40px;
}
.task-container {
	-ms-flex-direction: column;
	    flex-direction: column;
}
.task-container section {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	margin-top: 20px;
}
.task-container section :first-child {
	display: block;
	width: 10%;
	line-height: 40px;
}
.task-container section :nth-child(2) {
	display: block;
	min-width: 70%;
	line-height: 40px;
}
.task-container section :nth-child(3),
.task-container section :last-child {
	width: 15%;
	height: 40px;
}
.task-container section :last-child {
	width: 15%;
	height: 40px;
}
.error {
	border: 1px solid red;
}
.err-text {
	color: red;
}
.sort-container {
	margin: 0;
	display: -ms-flexbox;
	display: flex;
}
.sort-container input {
	height: 40px;
}
.login-admin {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	background: grey;
}
.login-admin input {
	margin: 5px;
}



